import { VaultUnstableHistogram } from '@/apis/path'
import { useFetch } from '@cetus/hooks'
import { formatTimestamp } from '@cetus/utils'
import { identity, pickBy } from 'lodash-es'

export type GetHistogramDataParams = {
  vaultID: string
  type?: 'hour' | 'day' | 'week' | 'month'
  beginTimestamp: number | string
  endTimestamp: number | string
}

export default function useGetVaultHistogram() {
  const { fetchByApi } = useFetch()

  const getVaultHistogram = async (params: GetHistogramDataParams) => {
    console.log('ğŸš€ ~ getVaultHistogram ~ params:', params)
    const requestParams: any = { ...params }
    requestParams['dateType'] = params.type == 'day' || params.type == 'week' ? 'hour' : 'day'
    delete requestParams['type']
    try {
      const options = pickBy(requestParams, identity)
      const res = await fetchByApi(VaultUnstableHistogram, 'GET', options)

      if (!res?.list) return []

      return res?.list.map((item: any, index: number) => {
        const timestampMs = item.Timestamp * 1000
        const date = new Date(timestampMs)
        const monthIndex = Number(date.toISOString().slice(5, 7)) // è·å–æœˆä»½

        const xAxis = getXData(res?.list?.length, date, params?.type, index)

        return {
          num: +item.value, // å¿«é€Ÿè½¬æ¢ä¸ºæ•°å­—
          date: item.Timestamp,
          // xAxis: params.dateType === 'mon' ? Month[monthIndex] : dayStrZeroPadding(date.toISOString().slice(0, 10)),
          xAxis,
          tooltipTime: formatTimestamp(timestampMs)
        }
      })
    } catch (error) {
      console.error('ğŸš€ ~ useGetHistogramData ~ getHistogramData Error:', error)
      return []
    }
  }
  const getXData = (dataLength: number, value: any, type: string = 'D', index: number) => {
    const dateType = type == 'D' || type == 'day' ? 'D' : type == 'W' || type == 'week' ? 'W' : 'M'
    console.log('ğŸš€ ~ getXData ~ type:', type)
    const date = value
    const noShowHours = date.getUTCHours()
    let hours = date.getUTCHours()
    const minutes = date.getUTCMinutes()
    const day = date.getUTCDate()
    const month = date.getUTCMonth() + 1 // æœˆä»½ä» 0 å¼€å§‹
    const period = hours >= 12 ? 'PM' : 'AM'
    hours = hours % 12 || 12 // 0 å˜ä¸º 12ï¼Œ13 å˜ä¸º 1
    if (dateType == 'D') {
      // dateTypeä¸ºå¤©çš„è¯ï¼Œæ˜¾ç¤ºå…·ä½“ UTC æ—¶é—´ (06:00, 12:00, 18:00, 00:00)
      return `${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')} ${period}`
    } else {
      if (dateType == 'M') {
        // dateTypeä¸ºæœˆçš„è¯ï¼Œæ˜¾ç¤ºå…·ä½“çš„å¤©
        return `${month}/${day}`
      } else {
        console.log('ğŸš€ ~ getXData ~ noShowHours:', noShowHours)
        // å¦‚æœæ•°æ®ä¸ºå‘¨,é‚£ä¹ˆå°äºä¸€å¤©ï¼Œå±•ç¤ºå¹³å‡ä¸¤ä¸ªç‚¹ï¼Œå¤§äºä¸€å¤©å°äºäº”å¤©ï¼Œæ¯å¤©å±•ç¤ºä¸¤ä¸ªç‚¹ï¼Œå¤§äºç­‰äº5å¤©ï¼Œæ¯å¤©å±•ç¤ºä¸€ä¸ªç‚¹
        if (dataLength < 24) {
          const step = Math.floor(dataLength / 2)
          console.log('ğŸš€ ~ getXData ~ step:', dataLength, index, index % step === 0, step)
          if (index % step === 0) {
            return `${month}/${day}`
          }
        }
        if (dataLength >= 24 && dataLength < 120) {
          if (noShowHours === 0 || noShowHours === 12) {
            return `${month}/${day}`
          }
        }
        if (dataLength >= 120) {
          if (noShowHours === 0 && minutes === 0) {
            return `${month}/${day}`
          }
        }
      }
    }
    return '' // å…¶ä»–æ—¶é—´ç‚¹ä¸æ˜¾ç¤º
  }

  return { getVaultHistogram }
}
