import Slippage from '@/components/common/Slippage'
import useGetApiVaultInfo from '@/hooks/vaults/useGetApiVaultInfo'
import { useGetRewardPeDay } from '@/hooks/vaults/useVaultsHelper'
import { Block, SelectTab } from '@cetus/design'
import { useAccountBalance } from '@cetus/hooks'
import { useAccountStore } from '@cetus/stores'
import { BackButton, NoData, RefreshButton } from '@cetus/ui-kit'
import { Box, Button, HStack, Stack, Text, VStack } from '@chakra-ui/react'
import { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { MyLiquidity } from './MyLiquidity'
import { PositionRange } from './PositionRange'
import PriceRangeChartPageBlock from './PriceRangeChartPageBlock'
import TvlChartPageBlock from './TvlChartPageBlock'

import vaultInterface from '@/hooks/vault-interface'
import useVaultsStore from '@/store/vaults/useVaultsList'
import { isAvailableObject } from '@cetus/utils'
import { VaultsAdd } from './VaultsAdd'
import { VaultsComposition } from './VaultsComposition'
import { VaultsHeader } from './VaultsHeader'
import { VaultsRemove } from './VaultsRemove'

const tabList = [
  {
    label: 'Deposit',
    value: 'Deposit'
  },
  {
    label: 'Withdraw',
    value: 'Withdraw'
  }
]

export default function VaultsDetailPage() {
  const { vaultId, category } = useParams()
  const { currentAccount } = useAccountStore()
  const { apiVaultInfo, getApiVaultInfo } = useGetApiVaultInfo(vaultId)
  const { fetchAccountBalance } = useAccountBalance()
  const useGetContractVaultInfo = vaultInterface.useGetContractVaultInfo(category == 'haedal')
  const { vaultStatsObj } = useVaultsStore()
  const [isAvailableVaultId, setIsAvailableVaultId] = useState(true)
  useEffect(() => {
    console.log('ğŸš€ ~ VaultsDetailPage ~ vaultStatsObj:', Object.values(vaultStatsObj), vaultStatsObj, vaultId)
    if (vaultId && isAvailableObject(vaultStatsObj)) {
      const res = Object.values(vaultStatsObj)?.filter((item: any) => {
        return item.vaultsId == vaultId
      })
      console.log('ğŸš€ ~ arr ~ arr:', res)
      if (res?.length > 0) {
        setIsAvailableVaultId(true)
      } else {
        setIsAvailableVaultId(false)
      }
    }
  }, [vaultId, vaultStatsObj])
  const {
    contractVault,
    lpTokenInfo,
    minPrice,
    maxPrice,
    vaultCoinAmounts,
    vaultsCoinAValue,
    vaultsCoinBValue,
    currPrice,
    balanceInfo,
    totalValue,
    fetchClmmPoolFromContract,
    fetchVaultInfoFromContract,
    vaultsCoinPercent,
    sharePoolRate,
    holdCoinAmounts,
    holdCoinAValue,
    holdCoinBValue,
    fetchVaultBalance,
    vaultTvl,
    depositRatio,
    hardCapUSD,
    performanceFee
  } = useGetContractVaultInfo(
    apiVaultInfo?.vaultsId,
    apiVaultInfo?.clmmPoolAddress,
    apiVaultInfo?.tokenA,
    apiVaultInfo?.tokenB,
    apiVaultInfo?.isReverse
  )
  console.log('ğŸš€ğŸš€ğŸš€ ~ VaultsDetailPage.tsx:81 ~ VaultsDetailPage ~ totalValue:', totalValue)
  console.log('ğŸš€ğŸš€ğŸš€ ~ VaultsDetailPage.tsx:61 ~ VaultsDetailPage ~ vaultTvl:', vaultTvl)

  const [currTab, setCurrTab] = useState<'Deposit' | 'Withdraw'>('Deposit')
  useEffect(() => {
    if (currentAccount?.address && apiVaultInfo && apiVaultInfo.vaultsId && lpTokenInfo?.decimals && category == 'haedal') {
      fetchVaultBalance(currentAccount?.address, apiVaultInfo.vaultsId, lpTokenInfo?.decimals)
    }
  }, [currentAccount?.address, apiVaultInfo, lpTokenInfo?.decimals, category])

  const { rewardPerA, rewardPerB } = useGetRewardPeDay(balanceInfo?.balanceFormat, apiVaultInfo)

  useEffect(() => {
    if (vaultId) {
      console.log('ğŸš€ğŸš€ğŸš€ ~ VaultsDetailPage.tsx:87 ~ useEffect ~ fetchVaultInfoFromContract:', fetchVaultInfoFromContract)
      fetchVaultInfoFromContract(vaultId, true)
    }
  }, [vaultId])

  useEffect(() => {
    if (apiVaultInfo) {
      fetchClmmPoolFromContract(apiVaultInfo.clmmPoolAddress, true)
    }
  }, [apiVaultInfo?.clmmPoolAddress])

  useEffect(() => {
    if (currentAccount?.address) {
      fetchAccountBalance()
    }
  }, [])

  const [chartRefresh, setChartRefresh] = useState(false)

  const autoRefresh = () => {
    // åˆ·æ–°ä½™é¢
    fetchAccountBalance()

    // åˆ·æ–° clmm åˆçº¦
    if (apiVaultInfo) {
      fetchClmmPoolFromContract(apiVaultInfo.clmmPoolAddress, true)
    }

    // åˆ·æ–°Vaults åˆçº¦
    if (vaultId) {
      fetchVaultInfoFromContract(vaultId, true)
      getApiVaultInfo(vaultId, true)
    }
    setChartRefresh(true)
    setTimeout(() => {
      setChartRefresh(false)
    }, 1000)
  }

  const vaultTabList = [
    {
      label: 'Overview',
      value: 'Overview'
    },
    {
      label: 'Analytics',
      value: 'Analytics'
    }
  ]

  const [vaultCurrTab, setVaultCurrTab] = useState('Overview')
  const navigate = useNavigate()
  return (
    <Box pt="40px" w="100%">
      {isAvailableVaultId ? (
        <VStack gap="20px" w="100%" align="flex-start">
          <HStack w="100%" justify="space-between">
            <BackButton bg="bg_six" borderColor="border_secondary" />
            {/* <RefreshButton
          handleRefresh={autoRefresh}
          isAutoRefresh
          refreshInterval={20}
          innerStyle={{ bg: 'background' }}
          w="32px"
          h="32px"
          borderRadius="8px"
          bg="background"
        /> */}
          </HStack>
          {/* å¤´éƒ¨åŒºåŸŸ */}
          <VaultsHeader apiVaultInfo={apiVaultInfo} isUnstableVault={category == 'haedal'} vaultTvl={vaultTvl} performanceFee={performanceFee} />
          <Stack flexDir={{ base: 'column-reverse', lg: 'row' }} w="100%" justifyContent="space-between" gap="16px" alignItems="start">
            {/* å·¦è¾¹åŒºåŸŸ */}
            <VStack width="100%" flex="1" gap="8px">
              {apiVaultInfo?.category == 'haedal' && (
                <SelectTab
                  type="borderTab"
                  wrapStyle={{
                    w: '100%',
                    h: '60px',
                    // border: 'none',
                    padding: '20px',
                    bg: 'bg_six',
                    borderColor: 'border_secondary'
                  }}
                  itemStyle={{
                    fontSize: '16px',
                    mr: '28px'
                  }}
                  tabList={vaultTabList}
                  currentTab={vaultCurrTab}
                  handleChangeTab={(item: any) => {
                    setVaultCurrTab(item?.value)
                  }}
                />
              )}

              {vaultCurrTab == 'Overview' ? (
                <>
                  {/* ä»·æ ¼åŒºé—´ */}
                  <PositionRange
                    minPrice={minPrice}
                    maxPrice={maxPrice}
                    // currentPrice="3.346919"
                    currentPrice={apiVaultInfo?.isReverse ? 1 / currPrice : currPrice}
                    displayTokenB={apiVaultInfo?.isReverse ? apiVaultInfo?.displayTokenA : apiVaultInfo?.displayTokenB}
                    displayTokenA={apiVaultInfo?.isReverse ? apiVaultInfo?.displayTokenB : apiVaultInfo?.displayTokenA}
                    isReverse={apiVaultInfo?.isReverse || false}
                  />
                  {/* æ± å­ token åˆ†é… */}
                  <VaultsComposition
                    vaultCoinAmounts={vaultCoinAmounts}
                    vaultsCoinAValue={vaultsCoinAValue}
                    vaultsCoinBValue={vaultsCoinBValue}
                    displayTokenB={apiVaultInfo?.displayTokenB}
                    displayTokenA={apiVaultInfo?.displayTokenA}
                    isReverse={apiVaultInfo?.isReverse || false}
                    vaultsCoinPercent={vaultsCoinPercent}
                    depositRatio={depositRatio}
                    hardCapUSD={hardCapUSD}
                    vaultTvl={vaultTvl}
                  />
                  {/* æˆ‘çš„æµåŠ¨æ€§ */}
                  <MyLiquidity
                    lpTokenInfo={lpTokenInfo}
                    sharePoolRate={sharePoolRate}
                    balanceInfo={balanceInfo}
                    totalValue={totalValue}
                    displayTokenB={apiVaultInfo?.displayTokenB}
                    displayTokenA={apiVaultInfo?.displayTokenA}
                    holdCoinAmounts={holdCoinAmounts}
                    holdCoinAValue={holdCoinAValue}
                    holdCoinBValue={holdCoinBValue}
                    isReverse={apiVaultInfo?.isReverse || false}
                    rewardPerA={rewardPerA}
                    rewardPerB={rewardPerB}
                  />
                </>
              ) : (
                <>
                  <TvlChartPageBlock isRefresh={chartRefresh} vaultsId={apiVaultInfo?.vaultsId} />
                  <PriceRangeChartPageBlock isRefresh={chartRefresh} vaultsId={apiVaultInfo?.vaultsId} isReverse={apiVaultInfo?.isReverse} />
                </>
              )}
            </VStack>

            {/* å³è¾¹åŒºåŸŸ */}
            <VStack w={{ base: '100%', lg: '40%' }} justifyContent="start" gap="8px">
              {/* åˆ‡æ¢Tab */}
              <Block p="0" borderRadius="12px" bg="bg_six" borderColor="border_secondary">
                <HStack w="100%" p={{ base: '0 8px', lg: '0 16px' }}>
                  <SelectTab
                    type="borderTab"
                    wrapStyle={{
                      w: '100%',
                      h: '60px',
                      border: 'none',
                      bg: 'bg_six',
                      borderColor: 'border_secondary'
                    }}
                    itemStyle={{
                      fontSize: '16px',
                      mr: '28px'
                    }}
                    tabList={tabList}
                    currentTab={currTab}
                    handleChangeTab={(item: any) => {
                      setCurrTab(item?.value)
                    }}
                  />
                  <HStack>
                    <Slippage slippageType="vaults" compact={true} />
                    <RefreshButton
                      handleRefresh={autoRefresh}
                      isAutoRefresh
                      refreshInterval={20}
                      innerStyle={{ bg: 'bg_secondary' }}
                      w="28px"
                      h="28px"
                      borderRadius="8px"
                      bg="bg_secondary"
                    />
                  </HStack>
                  {/* <MEVProtect /> */}
                </HStack>
              </Block>
              {currTab === 'Deposit' && (
                <VaultsAdd
                  apiVaultInfo={apiVaultInfo}
                  feeTier={apiVaultInfo?.feeDisplay}
                  tokenA={apiVaultInfo?.displayTokenA}
                  tokenB={apiVaultInfo?.displayTokenB}
                  isReverse={apiVaultInfo?.isReverse || false}
                  vaultsId={apiVaultInfo?.vaultsId}
                  totalLp={contractVault?.total_supply || contractVault?.lp_token_treasury}
                  isUnstableVault={category == 'haedal'}
                  handleRefreshClick={autoRefresh}
                  currentActionTab={currTab}
                />
              )}
              {currTab === 'Withdraw' && (
                <VaultsRemove
                  apiVaultInfo={apiVaultInfo}
                  holdCoinAmounts={holdCoinAmounts}
                  tokenA={apiVaultInfo?.displayTokenA}
                  tokenB={apiVaultInfo?.displayTokenB}
                  isReverse={apiVaultInfo?.isReverse || false}
                  vaultsId={apiVaultInfo?.vaultsId}
                  totalLp={contractVault?.total_supply}
                  holdLpAmount={balanceInfo?.balance}
                  isUnstableVault={category == 'haedal'}
                  handleRefreshClick={autoRefresh}
                  currentActionTab={currTab}
                />
              )}
            </VStack>
          </Stack>
        </VStack>
      ) : (
        <NoData type="custom" imgUrl="/images/img_create_pool.png" h="480px">
          <VStack>
            <Text h="20px" lineHeight="20px" fontSize="14px" color="text_caption">
              Pool address not found
            </Text>
            <Button
              mt="12px"
              w="120px"
              h="32px"
              borderRadius="8px"
              colorScheme="blue"
              fontSize="14px"
              fontWeight="500"
              onClick={() => {
                navigate('/vaults')
              }}
            >
              Back to Vault
            </Button>
          </VStack>
        </NoData>
      )}
    </Box>
  )
}
